import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import Home from './Home';
import Register from './Register';
import Login from './Login'
import Goals from './Goals';
import About from './About';
import History from './History';
import { useState, useEffect } from 'react';

function NavBar({ setPage }) {
    useEffect(() => {
        if (get_cookie("jwt")) {
            setLoggedIn(true);
        }

    }, [])
    const get_cookie = (name) => {
        return document.cookie.split(';').some(c => {
            return c.trim().startsWith(name + '=');
        });
    }

    const delete_cookie = (name, path, domain) => {
        if (get_cookie(name)) {
            document.cookie = name + "=";
        }
    }

    const logOut = () => {
        delete_cookie('jwt')
        setLoggedIn(false)
        setPage(() => Home)
    }

    const [loggedIn, setLoggedIn] = useState(false)
    return (
        <Navbar bg="light" expand="lg">
            <Container>
                <Navbar.Brand onClick={() => setPage(() => Home)}>Ballantyne's Health</Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="me-auto">
                        <Nav.Link onClick={() => setPage(() => Home)}>Home</Nav.Link>
                        <Nav.Link onClick={() => setPage(() => Goals)}>Goals</Nav.Link>
                        <NavDropdown title="Settings" id="basic-nav-dropdown">
                            <NavDropdown.Item onClick={() => setPage(() => History)}>History</NavDropdown.Item>
                            <NavDropdown.Item onClick={() => setPage(() => About)}>About</NavDropdown.Item>
                            <NavDropdown.Divider />
                            {loggedIn ? (
                                <NavDropdown.Item onClick={() => logOut()}>
                                    Log Out
                                </NavDropdown.Item>
                            ) : (

                                <div>
                                    <NavDropdown.Item onClick={() => setPage(() => Login)}>
                                        Log In
                                    </NavDropdown.Item>
                                    <NavDropdown.Item onClick={() => setPage(() => Register)}>
                                        Register
                                    </NavDropdown.Item>
                                </div>
                            )}
                        </NavDropdown>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    );
}

export default NavBar;