import React from 'react';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import api from '../services/apiHandler'
import { useState } from 'react';


export default function Register() {

  const [email, setEmail] = useState("");
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [password, setPassword] = useState("");
  const [passwordConfirm, setPasswordConfirm] = useState("");
  const [register, setRegister] = useState(false);
  
  const handleSubmit = (event) => {
    // prevent the page from refreshing
    event.preventDefault();
    if (password !== passwordConfirm) {
      alert('Passwords don\'t match')
      return
    }
    api
    .postNewUser(email, firstName, lastName, password)
    .then(retCode => {
      if (retCode === 201)
      {
        setRegister(true);
      }
    })
    .catch(err => {
      alert('Email already registered. Please use a different email')
    })
    
      //.then(response => {console.log(response.status)})
    //api.postNewUser(email, firstName, lastName, password)
  }

  return (
    <div>
      <h2>Register</h2>
      <Form onSubmit={(event) => handleSubmit(event)}>
        {/* email */}
        <Form.Group controlId="formEmail">
          <Form.Label>Email address</Form.Label>
          <Form.Control
            type="email"
            name="email"
            value={email}
            onChange={(event) => setEmail(event.target.value)}
            placeholder="Enter email"
          />
        </Form.Group>

        {/* First Name */}
        <Form.Group controlId="formFirstName">
          <Form.Label>First Name</Form.Label>
          <Form.Control
            type="text"
            name="firstName"
            value={firstName}
            onChange={(event) => setFirstName(event.target.value)}
            placeholder="First Name"
          />
        </Form.Group>

        {/* Last Name */}
        <Form.Group controlId="formLastName">
          <Form.Label>Last Name</Form.Label>
          <Form.Control
            type="text"
            name="lastName"
            value={lastName}
            onChange={(event) => setLastName(event.target.value)}
            placeholder="Last Name"
          />
        </Form.Group>

        {/* password */}
        <Form.Group controlId="formPassword">
          <Form.Label>Password</Form.Label>
          <Form.Control
            type="password"
            name="password"
            value={password}
            onChange={(event) => setPassword(event.target.value)}
            placeholder="Password"
          />
        </Form.Group>

        {/* password confirm */}
        <Form.Group controlId="formPasswordConfirm">
          <Form.Label>Confirm Password</Form.Label>
          <Form.Control
            type="password"
            name="passwordConfirm"
            value={passwordConfirm}
            onChange={(event) => setPasswordConfirm(event.target.value)}
            placeholder="Confirm Password"
          />
        </Form.Group>

        {/* submit button */}
        <Button
          variant="primary"
          type="submit"
          onClick={(event) => handleSubmit(event)}
        >
          Submit
        </Button>
        {/* display success message */}
        {register ? (
          <p className="text-success">You Have Registered Successfully. Please log in</p>
        ) : (
          <p></p>
        )}
      </Form>
    </div>

  )
}