import { useEffect, useState } from 'react';
import apiHandler from '../services/apiHandler'
import CardGroup from 'react-bootstrap/CardGroup'
import Alert from 'react-bootstrap/Alert'
import GoalCard from './GoalCard'



function History() {
    const [goals, setGoals] = useState([])
    const [loggedIn, setLoggedIn] = useState(true);
    useEffect(() => {
        apiHandler.getUserHistory()
            .then(userGoals => {
                setLoggedIn(true);
                console.log(userGoals)
                setGoals([...userGoals])
                console.log(goals)
            })
            .catch((err) => {
                setLoggedIn(false);
            })

    }, [])
    if (loggedIn) {
        return (
            <CardGroup>
                {goals.map((goal) => { return (<GoalCard key={goal._id} targetName={goal.target_name} targetDate={goal.target_date} targetValue={goal.target_value} currentValue={goal.current_value}></GoalCard>) })}
            </ CardGroup>
    
        )
    } else {
        return (
            <Alert variant="danger">
                <Alert.Heading>Looks like you aren't logged in!</Alert.Heading>
                <p>
                    Please log in or register to create and track your goals.
                </p>
            </Alert>
        )
    }

}

export default History;