import Card from 'react-bootstrap/Card'
import Button from 'react-bootstrap/Button';

export default function GoalCard({id, targetName, targetDate, currentValue, targetValue, editGoal}) {
    return (
        <Card style={{ width: '18rem' }}>
          <Card.Body>
            <Card.Title>{targetName}</Card.Title>
            <Card.Subtitle className="mb-2 text-muted">{targetDate}</Card.Subtitle>
            <Card.Text>
              Current Value: {currentValue}, Target Value: {targetValue}
            </Card.Text>
            {/* display success message */}
            {editGoal ? (
                <Button onClick={() => editGoal({id, targetName, targetDate, currentValue, targetValue}, true)} variant="primary" type="submit">
                    Edit Goal
                </Button>
            ) : (<div/>)}
            
          </Card.Body>
        </Card>
      );
}

