import Carousel from "react-bootstrap/Carousel";

export default function About() {
    return (
        <Carousel>
          <Carousel.Item>
            <img
              className="d-block w-100"
              src="https://images.pexels.com/photos/221210/pexels-photo-221210.jpeg"
              alt="First slide"
            />
            <Carousel.Caption>
              <h3>Fitness First Team</h3>
              <p>Founded by like minded individuals</p>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <img
              className="d-block w-100"
              src="https://images.pexels.com/photos/1028599/pexels-photo-1028599.jpeg"
              alt="Second slide"
            />
    
            <Carousel.Caption>
              <h3 className="text-dark">User Centred Motivation</h3>
              <p className="text-dark">Tailored plans to suit your every need</p>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <img
              className="d-block w-100"
              src="https://images.pexels.com/photos/235922/pexels-photo-235922.jpeg"
              alt="Third slide"
            />
    
            <Carousel.Caption>
              <h3>Made by a single man</h3>
              <p>
                All created by the ingenuity of an individual
              </p>
            </Carousel.Caption>
          </Carousel.Item>
        </Carousel>
      );
}