import axios from 'axios'
import bcrypt from 'bcryptjs';

const baseUrl = '/api'

const postNewUser = (email, firstName, lastName, password) => {
    //const hashedPassword = bcrypt.hash(password, 10)
    const newUserObject = {
        email: email,
        firstName: firstName,
        lastName: lastName,
        password: password
    }
    const request =  axios.post(`${baseUrl}/register`, newUserObject)
    return request.then(response => {
        return response.status
    })
}

const loginUser = (email, password) => {
    //const hashedPassword = bcrypt.hash(password, 10)
    const user = {
        email: email,
        password: password
    }
    const request = axios.post(`${baseUrl}/login`, user, {
        withCredentials: true
    })
    return request.then(response => response)
}

const getDefault = () => {
    const request = axios.get(baseUrl)
    console.log(request)
}

const getUserGoals = () => {
    const request = axios.get(`${baseUrl}/goals`, {
        withCredentials: true
    });
    return request.then(response => response.data)
}

const getUserHistory = () => {
    const request = axios.get(`${baseUrl}/history`, {
        withCredentials: true
    });
    return request.then(response => response.data)
}

const addNewGoal = (targetDate, targetName, targetValue, currentValue, dateHit) => {
    const goal = {
        targetDate,
        targetName,
        targetValue,
        currentValue,
        dateHit
    }
    const request = axios.post(`${baseUrl}/goals`, goal, {
        withCredentials: true
    })
    return request.then(response => response)
}

const updateGoal = (_id, targetDate, targetName, targetValue, currentValue, dateHit) => {
    console.log(_id)
    const goal = {
        _id,
        targetDate,
        targetName,
        targetValue,
        currentValue,
        dateHit
    }
    const request = axios.post(`${baseUrl}/goals/update`, goal, {
        withCredentials: true
    })
    return request.then(response => response)
}

const exports = {postNewUser, getDefault, loginUser, getUserGoals, getUserHistory, addNewGoal, updateGoal}
export default exports