import { useEffect, useState } from 'react';
import apiHandler from '../services/apiHandler'
import CardGroup from 'react-bootstrap/CardGroup'
import GoalCard from './GoalCard'
import Alert from 'react-bootstrap/Alert'
import Form from 'react-bootstrap/Form'
import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button';

const get_cookie = (name) => {
    return document.cookie.split(';').some(c => {
        return c.trim().startsWith(name + '=');
    });
}

function Goals() {
    const [goals, setGoals] = useState([])
    const [loggedIn, setLoggedIn] = useState(true);
    const [show, setShow] = useState(false);
    const [showSuccess, setShowSuccess] = useState(false);
    const [modalContent, setModalContent] = useState({});

    const handleClose = () => setShow(false);
    const handleSave = () => {
        if (modalContent.existingItem) {
            apiHandler
                .updateGoal(modalContent.id, modalContent.targetDate, modalContent.targetName, modalContent.targetValue, modalContent.currentValue, modalContent.dateHit)
                .then(() => {
                    setShow(false)
                    setShowSuccess(true);
                })
                .catch((err) => {
                })
        } else {
            apiHandler
            .addNewGoal(modalContent.targetDate, modalContent.targetName, modalContent.targetValue, modalContent.currentValue, modalContent.dateHit)
            .then(() => {
                setShow(false)
                setShowSuccess(true);
            })
            .catch((err) => {
            })
        }
        
    }
    const handleShow = (editingItem, existingItem) => {
        if (existingItem) {
            setModalContent({...editingItem, existingItem: true})
            console.log(editingItem)
            console.log(modalContent)
        } else {
            setModalContent({...editingItem, existingItem: false})
        }
        
        setShow(true);
    }
    
    useEffect(() => {
        apiHandler.getUserGoals()
            .then(userGoals => {
                setLoggedIn(true);
                setGoals([...userGoals])
            })
            .catch((err) => {
                setLoggedIn(false);
            })

    }, [])

    if (loggedIn) {
        return (
            <div>
                {showSuccess ? (
                    <Alert variant="Success">
                        <Alert.Heading>Goal added/updated succesffully!</Alert.Heading>
                        <p>
                            Please refresh to see all goals.
                        </p>
                    </Alert>) : 
                    (<div></div>)}
                <Button onClick={() => handleShow(false)} className='float-right'>Add Goal</Button>
                <CardGroup>
                    {goals.map((goal) => { return (<GoalCard key={goal._id} id={goal._id} targetName={goal.target_name} targetDate={goal.target_date} targetValue={goal.target_value} currentValue={goal.current_value} editGoal={handleShow}></GoalCard>) })}
                </ CardGroup>

                <Modal show={show} onHide={handleClose}>
                    <Modal.Header closeButton>
                        <Modal.Title>Editing Goal</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form>
                            <Form.Group controlId="targetName">
                                <Form.Label>Target Name</Form.Label>
                                <Form.Control
                                    name="targetName"
                                    value={modalContent.targetName}
                                    onChange={(event) => setModalContent({ ...modalContent, targetName: event.target.value })}
                                />
                            </Form.Group>
                            <Form.Group controlId="targetDate">
                                <Form.Label>Target date</Form.Label>
                                <Form.Control
                                    name="targetDate"
                                    value={modalContent.targetDate}
                                    onChange={(event) => setModalContent({ ...modalContent, targetDate: event.target.value })}
                                />
                            </Form.Group>
                            <Form.Group controlId="targetValue">
                                <Form.Label>Target Goal</Form.Label>
                                <Form.Control
                                    name="targetValue"
                                    value={modalContent.targetValue}
                                    onChange={(event) => setModalContent({ ...modalContent, targetValue: event.target.value })}
                                />
                            </Form.Group>
                            <Form.Group controlId="currentValue">
                                <Form.Label>Current Value</Form.Label>
                                <Form.Control
                                    name="currentValue"
                                    value={modalContent.currentValue}
                                    onChange={(event) => setModalContent({ ...modalContent, currentValue: event.target.value })}
                                />
                            </Form.Group>
                            <Form.Group controlId="dateHit">
                                <Form.Label>Date Achieved</Form.Label>
                                <Form.Control
                                    name="dateHit"
                                    value={modalContent.dateHit}
                                    onChange={(event) => setModalContent({ ...modalContent, dateHit: event.target.value })}
                                />
                            </Form.Group>
                        </Form>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={handleClose}>
                            Close
                        </Button>
                        <Button variant="primary" onClick={handleSave}>
                            Save Changes
                        </Button>
                    </Modal.Footer>
                </Modal>
            </div>
        )
    } else {
        return (
            <Alert variant="danger">
                <Alert.Heading>Looks like you aren't logged in!</Alert.Heading>
                <p>
                    Please log in or register to create and track your goals.
                </p>
            </Alert>
        )
    }

}

export default Goals;